/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  position: relative;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
*, *:before, *:after {
  transition: 0.2s ease-in-out;
}

html, body {
  font-family: system-ui, helvetica neue, helvetica, arial, sans-serif;
  font-weight: 200;
}

.switches {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 60px;
}
@media (min-width: 800px) {
  .switches {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .switches {
    grid-template-columns: repeat(3, 1fr);
  }
}

.light-switch {
  opacity: 0;
  position: absolute;
  left: -9999px;
  height: 1px;
  width: 1px;
}
.light-switch:checked ~ #background {
  background-color: #ebdbac;
}
.light-switch:checked + .light-switch-label {
  box-shadow: 0 2px 2px #d0b57b;
}
.light-switch:checked + .light-switch-label .switch {
  box-shadow: 0 10px 10px -5px rgb(233, 219, 176), 0 0 0 1px rgba(0, 0, 0, 0.1), 0 0 0 4px #fff4d3, 0 0 0 5px rgba(0, 0, 0, 0.1);
}
.light-switch:checked + .light-switch-label .switch:before {
  height: 75px;
  top: 0px;
  line-height: 75px;
  background: #fff4d3;
  color: #64bf60;
  text-shadow: 0 0 3px #38ff2e;
}
.light-switch:checked + .light-switch-label .switch:after {
  height: 70px;
  bottom: 5px;
  line-height: 70px;
  background: #fffaea;
  box-shadow: 0 5px 0 #d0b57b;
  color: #d7bf95;
  text-shadow: 0 0 0px transparent;
}

.light-switch-label {
  position: relative;
  display: block;
  height: 300px;
  width: 200px;
  z-index: 1;
  margin: 0 auto;
  background-color: #fff4d3;
  border-radius: 10px;
  border: 1px solid #ffdb6d;
  box-shadow: 0 0 4px rgba(19, 36, 54, 0.5);
}
.light-switch-label .switch {
  cursor: pointer;
  height: 150px;
  width: 100px;
  background: #fffaea;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -75px 0 0 -50px;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 10px 10px -5px rgba(233, 219, 176, 0), 0 0 0 1px rgba(0, 0, 0, 0.1), 0 0 0 4px #fff4d3, 0 0 0 5px rgba(0, 0, 0, 0.1);
}
.light-switch-label .switch:before {
  content: "ON";
  display: block;
  position: absolute;
  height: 70px;
  text-align: center;
  line-height: 70px;
  width: 100px;
  top: 5px;
  left: 0;
  background: #efe0b1;
  color: #bfa371;
  border-radius: 5px 5px 0 0;
}
.light-switch-label .switch:after {
  content: "OFF";
  display: block;
  position: absolute;
  height: 75px;
  text-align: center;
  line-height: 75px;
  width: 100px;
  bottom: 0;
  left: 0;
  background: #fff4d3;
  color: #a4441a;
  text-shadow: 0 0 3px #ff4e00;
  border-radius: 0 0 5px 5px;
}
.light-switch-label .screw {
  position: absolute;
  height: 16px;
  width: 16px;
  border-radius: 100%;
  left: 50%;
  margin-left: -8px;
  background: #e3d4a5;
  overflow: hidden;
  box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.15);
}
.light-switch-label .screw:before {
  content: "";
  display: block;
  position: absolute;
  height: 100%;
  width: 2px;
  left: 50%;
  margin-left: -1px;
  background: rgba(0, 0, 0, 0.2);
}
.light-switch-label .screw:first-of-type {
  top: 30px;
}
.light-switch-label .screw:last-of-type {
  bottom: 30px;
}

#background {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #132436;
}